*, body, head {
    margin: 0;
    padding: 0;
}
.wrapper {
    width: 100%;
}

.header {
    border: 1px solid red;
    width: 75%;
    height: 80px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
}

.top-line {
    width: 100%;
    background: #000;
    height: 7px;
    position: absolute;
    top: 0;
}
.logo{
    display: inline-block;
    vertical-align: middle;
}

.logo-item {
    vertical-align: middle;
}
.logo-gold {
    padding-top: 12px;
    overflow: auto;
}
.logo-text {
    font-size: 1.4em;
    line-height: 1rem;
}
.menu {
    float: right;
    width: 30%;
}
.menu-item {
    color: #595959;
    padding-top: 20%;
    display: inline-block;
    list-style: none;
    padding-right: 2.5%;
}
.logo-color, .menu-item-theme {
    color: #c42d21;
}
.menu-item-border {
    border-right: 2px solid #595959;
}